import React from 'react';
class ThinkingInReact extends React.Component {
    constructor(props) {
        super(props)

    }

    render() {
        const renderData = data.map((data) => {
            return <span>category: {data.category}, price: {data.price}, name: {data.name}<br /></span>
        })
        return (
            <div className="style">
                <h1>Unit 12: Thinking In React</h1>
                <div className="content">
                    <h3>Start With A Mock</h3>
                    <strong>Our JSON API returns some data that looks like this:</strong><br />
                    {renderData}
                </div><br />
                <div className="content">
                    <h3>Step 1: Break The UI Into A Component Hierarchy</h3>
                    <p>Bc u tin ta cn to cy component t bn thit k</p>
                    <p><strong>* FilterableProductTable</strong><br />
                    </p>
                    <p style={{ paddingLeft: "20px" }}>
                        <strong>* SearchBar</strong><br />
                        <strong>* ProductTable</strong>
                    </p>
                    <p style={{ paddingLeft: "40px" }}>

                        <strong>* ProductCategoryRow</strong> <br />
                        <strong>* ProductRow</strong>
                    </p>

                </div><br />
                <div className="content">
                    <h3>Step 2: Build A Static Version in React</h3>
                    <p>Xy dng giao din tnh v ti s dng cc component bng props</p>
                </div><br />
                <div className="content">
                    <h3>Step 3: Identify The Minimal (but complete) Representation Of UI State</h3>
                    <p>S dng State  kch hot cc thay i ca giao din</p><br />
                    <p> xc nh c phi State hay khng ta da vo 3 iu kin di y</p><br />
                    <p><strong>1.C phi n c truyn t component cha qua props khng? Nu c th n c th khng phi l state.</strong><br />
                        <strong> 2.D liu c thay i khng? nu khng th n khng phi l state.</strong><br />
                        <strong>3.Bn c th tnh ton n t cc state hay props khc trong component cu bn khng? nu c th n khng phi l state.</strong></p>
                </div><br />
                <div className="content">
                    <h3>Step 4: Identify Where Your State Should Live</h3>
                    <p><strong> xc nh v tr ca state ta cn da theo 4 iu di y:</strong></p>
                    <p>Xc nh tt c cc component s hin th da trn state.<br />
                        Tm ra mt component cha ( component  pha trn cc component cn state  trong h thng phn chia cp bc).<br />
                        Hoc l component cha hay component khc  pha trn nn gi state.<br />
                        Nu bn khng th tm ra component hp l, th hy to ra mt component mi nm gi state v thm n vo trong h thng phn chia cp bc  pha trn component cha.</p>
                </div><br />
                <div className="content">
                    <h3>Step 5: Add Inverse Data Flow</h3>
                    <p> xy dng c ng dng hin th chnh xc cc gi tr th ta s dng props v state nhng  d liu c th truyn t component con ln component cha th ta dng state  thc hin iu </p>
                </div>
            </div>
        )
    }
}
const data = [
    { category: "Sporting Goods", price: "$49.99", stocked: true, name: "Football" },
    { category: "Sporting Goods", price: "$9.99", stocked: true, name: "Baseball" },
    { category: "Sporting Goods", price: "$29.99", stocked: false, name: "Basketball" },
    { category: "Electronics", price: "$99.99", stocked: true, name: "iPod Touch" },
    { category: "Electronics", price: "$399.99", stocked: false, name: "iPhone 5" },
    { category: "Electronics", price: "$199.99", stocked: true, name: "Nexus 7" }
];
export default ThinkingInReact;