const StateAndLifecycle = () => {
    return (
        <div className="style">
            <h1>Unit 5: State and Lifecycle</h1>
            <div className="content">

                <h3>
                    Converting a Function to a Class
                </h3>
                <p>
                    You can convert a function component like Clock to a class in five steps:<br />

                    1.To mt ES6 "class", cng tn, cho n "extends React.Component"<br />
                    2.Thm mt phng thc render().<br />
                    3.Di chuyn tt c ni dung ca function vo bn trong phng thc render()<br />
                    4.Thay th "props" thnh "this.props" trong ni dung ca render().<br />
                    5.Xa function rng m ta  ly ni dung t n.
                </p>
                <label>class Clock extends React.Component {"{"}</label><br />
                <label>render() {"{"}</label><br />
                <label>return (<br />
                    &lt;div&gt;<br />
                    &lt;h1&gt;Hello, world!&lt;/h1&gt;<br />
                    &lt;h2&gt;It is{"{this.props.date.toLocaleTimeString()}"} .&lt;/h2&gt;<br />
                    &lt;/div&gt;
                    );</label>
            </div><br />
            <div className="content">
                <h3>Adding Local State to a Class</h3>
                <p>Thm mt class constructor c tham s truyn vo l props</p>
                <p>Thm 1 super(props) vo trong constructor</p>
                <p>Tt c state u nm trong constructor</p>
            </div><br />
            <div className="content">
                <h3>Adding Lifecycle Methods to a Class</h3>
                <strong>These methods are called lifecycle methods:</strong>
                <p>componentDidMount() {"{"}  {"}"}: chy sau khi component  c render vo trong DOM. </p>
                <p>componentWillUnmount() {"{"}  {"}"}: chy sau khi component kt thc qu trnh render.</p>
            </div><br />
            <div className="content">
                <h3>Using State Correctly</h3>
                <strong>Do Not Modify State Directly</strong><br />
                <strong>State Updates May Be Asynchronous</strong><br />
                <strong>State Updates are Merged</strong><br />
            </div><br />
            <div className="content">
                <h3>The Data Flows Down</h3>
                <p>Mt component c th truyn State ca n xung component con</p>
            </div>
        </div>
    )
}
export default StateAndLifecycle;