export default function RenderingElement() {
    return (
        <div className="style">

            <h1>Unit 3: Rendering Elements</h1>
            <div className="content">
                <h3>
                    Rendering an Element into the DOM
                </h3>
                <p>to 1 bin c tn l root bng ReactDOM gi ti hm createRoot, trong hm createRoot gi phng thc document.getElementByID truyn tham s root vo.<br />
                    to 1 bin c tn element to cc th tag, sau  t bin root gi ti hm render v truyn tham s element v.
                </p>
                <p>
                    const root = ReactDOM.createRoot(
                    document.getElementById('root')
                    );
                    <p>const element = &lt;h1&gt;Hello, world&lt;/h1&gt;;</p>
                    root.render(element);
                </p>
            </div><br />
            <div className="content">
                <h3>Updating the Rendered Element</h3>
                <p>
                    const root = ReactDOM.createRoot(
                    document.getElementById('root')
                    );
                </p>
                <label>function tick() {"{"}</label><br />
                <label>const element = (</label><br />
                <label>
                    &lt;div&gt;<br />
                    &lt;h1&gt;Hello, world!&lt;/h1&gt;
                    &lt;h2&gt;It is {"{new Date().toLocaleTimeString()}"}.&lt;/h2&gt;<br />
                    <label> &lt;/div&gt;</label>
                    );<br />
                    root.render(element);<br />
                    {"}"}<br />
                    setInterval(tick, 1000);
                </label><br />
            </div><br />
            <div className="content">
                <h3>React Only Updates Whats Necessary</h3>
                <p>
                    React DOM so snh cc phn t v cc thnh phn con ca n vi ci trc , nu c thay i th s cp nht li DOM.
                </p>
            </div>

        </div>
    )
}