import React from 'react';
class ComponentInheritance extends React.Component {
    constructor(props) {
        super(props)
    }
    render() {
        return (
            <div className="style">
                <h1>Unit 11: Component Inheritance</h1>
                <div className="content">
                    <h3>Containment</h3>
                    <p>Thay v dng component nh:<br />
                        <strong>{" <Component/>"}</strong><br />
                        ta c th dng cch khc nh:<br />
                        <strong> {"<Component>Ni dung</Component>"}</strong>
                    </p>
                </div><br />
                <div className="content">
                    <h3>Specialization</h3>
                    <p>C th ti s dng component bng cch s dng props</p><br />
                    <p>
                        <strong>function Dialog(props) {"{"}</strong><br />
                        return (<br />
                        &lt;FancyBorder color="blue"&gt;<br />
                        &lt;h1 className="Dialog-title"&gt;<br />
                        {"{props.title}"}<br />
                        &lt;/h1&gt;<br />
                        &lt;p className="Dialog-message"&gt;<br />
                        {"{props.message}"}<br />
                        &lt;/p&gt;<br />
                        &lt;/FancyBorder&gt;<br />
                        );<br />
                        {"}"}<br />

                        <strong>function WelcomeDialog() {"{"}</strong><br />
                        return (<br />
                        &lt;Dialog<br />
                        title="Welcome"<br />
                        message="Thank you for visiting our spacecraft!" /&gt;<br />
                        );<br />
                        {"}"}
                    </p>
                </div><br />
                <div className="content">
                    <h3>So What About Inheritance?</h3>
                    <p>Props v composition cn  tu chnh giao hin v hnh vi mt cch r rng v an ton. <br />Cc component nhn cc props khng gii hn, k c cc gi tr s khai, cc phn t React hoc cc hm.</p>
                </div><br />

            </div>
        )
    }
}
export default ComponentInheritance;